button{
    background-color: yellow;
    margin: 20px;
    width: 200px;
    height: 50px;
    line-height: 50px;
    border-radius: 20px;
    box-shadow: 0 3px 0px #3c354a;
    transition-duration: 1s;
}

.question{
    font-size: 30px;
}

.clicked {
    color: yellow;
    background-color: blue;
    transition-duration: 2s;
    box-shadow: 0 3px 0px #3c354a;
    font-size: 20px;
}

.correct{
    outline: green 6px solid;
}

button:hover{
    font-size: 20px;
    transition-duration: 1s;
}